.favourites-coffee{
    margin-top: 100px;
    &__title{
        @include heading-2();
        text-align: center;
        
        
        &-accent{
            color: $textAccent;
            font-style: oblique 10deg;
        }
    }
    &__description{
        @include medium();
        line-height: 24px;
    }
    &__arrows{
        display: none;
        @media ($tablet) {
            display: block;
        }
    }
    &__slider{
        display: flex; 
        flex-direction: row; 
        justify-content: space-between;
        height: 576px;
        @media ($phone) {
            height: 684px;
        }
        
        
        
    }
}