.enjoy{
    
    overflow: hidden;

    margin-top: 20px;
    @media ($tablet) {
        margin-top: 1rem;
    }
    @media ($dekstop) {
        margin-top: 20px;
    }
    &-background{
        width: 100%;
        height: 552px;
        @media ($tablet) {
            height: 644px;
        }
        @media ($dekstop) {
            height: 644px;
        }
        border-radius: 40px;
    }
    &__list{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 90.8045977011%;
        height: 492px;
        margin: 0 auto;
        margin-left: 16px;
        margin-right: 16px;
        @media ($tablet) {
            margin-left: 3.8rem;
            margin-right: 79px;
            max-width: 530px;
            height: 504px;
        }
        @media ($dekstop) {
            height: 544px;
            margin: 0 0 100px 100px;
        }
        
        &-title{
            
            margin-top: 45px;
            @media ($tablet) {
                margin-top: 6rem;
            }
            @media ($dekstop) {
                margin-top: 100px;
            }
            
            @include heading-1();
            color: $textLight;
            &__accent{
                color: $textAccent;
                font-style: oblique 10deg;
                line-height: 75.6px;
            }
        }
        &-description{
            @include medium();
            color: $textLight;
            
            margin-bottom: 10px;
            @media ($tablet) {
                margin-top: 2.8rem;
                margin-bottom: 0;
            }
            @media ($dekstop) {
                margin-top: 0;
                margin-bottom: 0;
            }
        }
    }
}