.menu{
    &__info{
        height: 12.7rem;
        @media ($tablet) {
            height: 311px;
        }
        
        @media ($dekstop) {
            height: 236px;
        }
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    &__title{
        margin: 0 auto;
        width: 97%;
        @media ($dekstop) {
            width: 800px;
        }
        
        @include heading-2();
        text-align: center;
        &-accent{
            color: $textAccent;
            font-style: oblique 10deg;
        }
    }
    &__buttons{
        width: 100%;
        @media ($phone) {
            max-width: 361px;
        }
        
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin: 0 auto;
        
        
        &-item{
            background-color: inherit;
        }
    }
    &__cards{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        
        &-item{
            background-color: inherit;
            
            
            margin: 0 auto;
            margin-top: 40px;
            @media ($dekstop) {
                margin-right: 40px;
            }
        }
        &-item:nth-child(n+5){
            @media ($dekstop) {
                display: block;
            }
            display: none;
        }
        &-item:nth-child(4){
            @media ($dekstop) {
                margin-right: 0;
            }
            
        }
        &-item:nth-child(8){
            @media ($dekstop) {
                margin-right: 0;
            }
        }

    }
    &__refresh{
        @include buttons();
        display: block;
        justify-content: center;
        background-color: inherit;
        width: 60px;
        height: 60px;
        border-radius: 100px;
        margin: 0 auto;
        margin-top: 2.5rem;
        &-img{
            display: block;
            margin: 0 auto;
            padding-top: 1rem;
        }
        @media ($dekstop) {
            display: none;
        }

    }
}