.tab-item{
    display: flex;
    flex-direction: row;
    
    border: solid 0.1px;
    border-color: $borderLight;
    
    height: 46px;
    background-color: white;
    border-radius: $mainRadis;
    
    &__kettle{
        margin: auto;
        margin-left: 5px;
        margin-right: 0;
        border-radius: $mainRadis;
        background-color: $borderLight;
        width: 30px;
        height: 30px;
        

    } 

    &__img{

        margin: auto;
        padding-top: 7px;
    }

    &__text{
        @include linkButton();
        margin: auto;
        margin-left: 7px;
        margin-right: 10px;
    }
    

}
.tab-item:hover,.tab-item:active{
    background-color: $borderDark;
    border-color: $borderDark;
    .tab-item__text{
        color: $textLight;
    }
}
