.header-nav{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top:18px;
    
    
    
    &__text{
        display: none;
        @media (min-width: 769px) {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: center;
        }
        @include medium();
        margin: auto;
        width: 92%;
        height: 28px;
        margin-top: 16px;
        
        @media ($dekstop) {
            width: 100%;
            margin-left: 60px;
        }
        &-item{
            @include linkButton();
            height: 28px;
            margin-right: 2rem;
            @media ($dekstop) {
                margin-right: 56px;
            }
            
            scroll-behavior: smooth;
        }
        &-item:hover{
            border-bottom: solid 1px;
            border-bottom-color: $textDark;
        }
    }
    
    &__link{    
        display: none;
        @media (min-width: 769px) {
            display: block;
        }
        
        width: 82.5px;
        height: 28px;
        margin: auto;
        margin-right: 0;
    }
    &__burger{
        margin-top: 8px;
        @media (min-width: 769px) {
            display: none;
        }
    }
}