.mobile-app{
    
    @media ($dekstop) {
        padding-top: 100px;
    }
    
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @media ($dekstop) {
        flex-direction: row;
    }
    &__title{
        @include heading-2();
        text-align: left;
        margin-bottom: 35px;
        &-accent{
            color: $textAccent;
            font-style: oblique 10deg;
        }
    }
    &__info{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 100%;
        margin-top: 4rem;
        @media ($dekstop) {
            width: 630px;
            height: 353px;
            margin-top: 145px;
        }
        
        
        
    }
    &__buttons{
        display: flex;
        flex-direction: column;
        justify-content: left;
        margin-top: 0.7rem;
        @media ($tablet) {
            margin-top: 1rem;
            flex-direction: row;
        }
        @media ($dekstop) {
            margin-top: 0;
        }
       
        

    }
    &__button{
        
        margin: 12px 20px 12px 0;
        background-color: inherit;
    }
    &__screen{
        width: 100%;
        max-width: 630px;
        margin-top: 1.5rem;
        @media ($tablet) {
            margin: 0 auto;
            margin-top: 5.5rem;
           
        }
        @media ($dekstop) {
            margin-top: 0;
            margin-right: 0;
        }
        &-img{
            width: 100%;
            max-width: 630px;
            
            
        }
    }
    &__description{
        margin-bottom: 18px;
        
        @include medium();
    }
}