.footer-contacts{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: $containerColor;
    border-radius: 40px;
    height: 526px;
    margin-top: 6rem;
    @media ($tablet) {
        
        height: 736px;
    }
    
    @media ($dekstop) {
        flex-direction: row;
        justify-content: center;
        margin-top: 96px;
        height: 450px;
    }
    
    &__coffetime{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 316px;
        height: 186px;
        margin-left: 16px;
        margin-top: 4rem;
        @media ($tablet) {
            width: 80%;
            margin-left: 3.5rem;
            margin-top: 6.5rem;
        }
        @media ($dekstop) {
            margin-top: 0;
            width: 530px;
            height: 250px;
            margin-left: 0;
            margin-right: 95px;
        }
        
        
    }
    &__title{
        
        @media ($dekstop) {
            margin-top: 105px;
        }
        
        
        @include heading-2();
        text-align: left;
        color: $textLight;
        &-accent{
            color: $textAccent;
            font-style: oblique 10deg;
        }
    }
    &__icons{
        display: flex;
        flex-direction: row;
        margin-top: 40px;
        &-item{
            margin-right: 8px;
        }
    }
    &__us{
        
        width: 100%;
        height: 186px;
        margin-left: 16px;
        margin-bottom: 3.5rem;
        @media ($tablet) {
            margin-bottom: 6rem;
            margin-left: 3.5rem;
        }
        @media ($dekstop) {
            width: 530px;
            height: 250px;
            margin-left: 0;
            margin-bottom: 0;
        }
        
        
        &-title{
            
            @media ($dekstop) {
                margin-top: 135px;
            }
            
            
            @include heading-3();
            color: $textLight;
        }
        &-info{
            
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            width: 262px;
            height: 116px;
            margin-top: 38px;
        }

    }
}