@import './utils';
@import './components';
@import './layout';


body{
    background-color: $bodyColor;
}
.main{
    margin: 0;
    width: 380px;
}
.container{
    width: 100%;
    margin: auto;
    
}
.wrapper{
    width: 92%;
    @media ($tablet) {
        width: 89.5833333333%;
    }
    @media ($dekstop) {
        width: 94.4444444444%;
    }
    
    max-width: 1360px;
    margin: 0 auto;
    
    @media ($dekstop) {
        margin: auto;
    }

}
.section-list{
    
    
    margin: 0;
}
.flex-row{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    
}

.heading-1{
    @include heading-1();
}
.heading-2{
    @include heading-2();
}
.heading-3{
    @include heading-3();
}
.caption{
    @include caption();
}
.medium{
    @include medium();
}
.linkButton{
    @include linkButton();
}
.burger-link{
    @include burger-link();
}
.accent{
    @include accent();
}
.logo{
    width: 100px;
    height: 60px;
}
.app-img{
    width: 36px;
    height: 36px;
}
.coffeecup{
    width: 20px;
    height: 20px;
}
.primary-img{
    width: 20px;
    height: 20px;
}
.link-img{
    width: 20px;
    height: 20px;
}
.arrow-img{
    width: 24px;
    height: 24px;
}
.icon-img{
    width: 24px;
    height: 24px;
}
.content{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 2rem;
}
.flex-column{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}
