.slider{
    width: 100%;
    max-width: 480px;
    
    height: 640px;
    margin: auto;
    @media ($tablet) {
        margin-top: 0;
    }
    @media ($dekstop) {
        margin: auto;
    }
    
    &__img{
        width: 100%;
        
        margin: auto;
        margin-top: 18px;
        @media ($dekstop) {
            margin-top: 12px;
        }

        
    }
    &__text{ 
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 140px;
        text-align: center;
        margin-top: 20px;
        
        &-title{
            
            
            @include heading-3();
            line-height: 30px;
        }
        &-description{
            @include medium();
            line-height: 24px;
            margin-top: 16px;
            @media ($tablet) {
                margin-top: 0;
            }

        }
        &-price{
            margin-top: 1rem;
            @media ($tablet) {
                margin-top: 0;
                
            }
            @include heading-3();
            line-height: 30px;
        }
    }
}