.about{
    
    margin-top: 100px;
    &__title{
        @include heading-2();
        
        margin-bottom: 40px;
        &-accent{
            color: $textAccent; 
            font-style: oblique 10deg;
        }
    }
    &__images{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        
        @media ($dekstop) {
            flex-direction: row;
        }
        &-list{
            
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: 630px;
            @media ($dekstop) {
                height: 1060px;
            }
            
            &__item1{
                display: block;
                position: relative;
                width: 100%;
                max-width: 688px;
                @media ($dekstop) {
                    max-width: 660px;
                }
                height: 590px;
                border-radius: 20px
                

            }
            &__item2{
                display: none;
                @media ($dekstop) {
                    display: block;
                }
                width: 660px;
                height: 430px;
                border-radius: 20px;

            }
            &__item3{
                display: none;
                @media ($dekstop) {
                    display: block;
                }
                width: 660px;
                height: 430px;
                border-radius: 20px;
            }
            &__item4{
                width: 100%;
                max-width: 688px;
                @media ($dekstop) {
                    max-width: 660px;
                }
               
                height: 590px;
                border-radius: 20px
            }
        }
        
    }
}
